"use client"

import { useMemo, useState } from "react"
import { useRouter } from "next/navigation"
import { StepIndicator } from "@/components/onboarding/StepIndicator"
import { RadioGroupCard } from "@/components/onboarding/RadioGroupCard"
import { CheckboxGroupCard } from "@/components/onboarding/CheckboxGroupCard"
import { Button } from "@/components/ui/button"
import { doc, serverTimestamp, setDoc } from "firebase/firestore"
import { db } from "@/lib/firebaseClient"
import { useAuth } from "@/lib/auth-context"

const ageOptions = [
  { label: "18–24", value: "18-24" },
  { label: "25–34", value: "25-34" },
  { label: "35–44", value: "35-44" },
  { label: "45–54", value: "45-54" },
  { label: "55+", value: "55+" },
]

const genderOptions = [
  { label: "Male", value: "male" },
  { label: "Female", value: "female" },
  { label: "Non-binary", value: "non-binary" },
  { label: "Prefer not to say", value: "prefer-not-to-say" },
]

const sportsOptions = [
  { label: "Padel", value: "padel" },
  { label: "Tennis", value: "tennis" },
  { label: "Football", value: "football" },
]

type Gender = "male" | "female" | "non-binary" | "prefer-not-to-say"

export default function PersonalDetailsPage() {
  const router = useRouter()
  const { user } = useAuth();
  const [ageRange, setAgeRange] = useState<string | null>(null)
  const [gender, setGender] = useState<Gender | null>(null)
  const [sports, setSports] = useState<string[]>([])
  const [isSubmitting, setIsSubmitting] = useState(false)

  const isValid = useMemo(() => {
    return Boolean(ageRange && gender && sports.length >= 1)
  }, [ageRange, gender, sports])

  const handleContinue = async () => {
    if (!isValid) return
    setIsSubmitting(true)
    
    try {
        if (!user) {
            console.warn("[Onboarding] No user found when submitting onboarding data")
            return
        }

        const docPath = `players/${user.uid}`
        const onboardingData = {
          ageRange: ageRange ?? "",
          gender: gender ?? "prefer-not-to-say",
          sports,
          onboardingCompleted: true,
          updatedAt: serverTimestamp(),
        }

        console.info("[Onboarding] ========== SAVING ONBOARDING ==========")
        console.info("[Onboarding] user.uid:", user.uid)
        console.info("[Onboarding] docPath:", docPath)
        console.info("[Onboarding] data:", JSON.stringify({ ...onboardingData, updatedAt: "serverTimestamp()" }))
        
        await setDoc(doc(db, 'players', user.uid), {
          uid: user.uid,
          email: (user.email || "").toLowerCase().trim(),
          ...onboardingData,
        }, { merge: true });

        console.info("[Onboarding] SUCCESS: onboardingCompleted=true saved to", docPath)
        
        router.replace("/players")
    } catch (error) {
        console.error("[Onboarding] Error saving onboarding data:", error)
    } finally {
        setIsSubmitting(false)
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <div className="mx-auto flex max-w-3xl flex-col gap-8 px-4 pb-16 pt-10 sm:px-6 lg:px-8">
        <StepIndicator step={1} total={1} title="Tell us a bit about you" />

        <div className="rounded-3xl border bg-card p-6 shadow-sm sm:p-8">
          <p className="text-sm text-muted-foreground">
            This helps us match you with players like you. You can change this anytime.
          </p>

          <div className="mt-6 space-y-8">
            <RadioGroupCard
              label="How old are you?"
              value={ageRange}
              options={ageOptions}
              onChange={setAgeRange}
            />

            <RadioGroupCard
              label="How do you identify?"
              value={gender}
              options={genderOptions}
              onChange={(value) => setGender(value as Gender)}
            />

            <CheckboxGroupCard
              label="What sports do you play?"
              values={sports}
              options={sportsOptions}
              onChange={setSports}
            />
          </div>
        </div>

        <div className="flex flex-col gap-3 sm:flex-row sm:items-center sm:justify-between">
          <p className="text-xs text-muted-foreground">
            Estimated time: under 30 seconds
          </p>
          <Button
            onClick={handleContinue}
            disabled={!isValid || isSubmitting}
            className="h-11 rounded-full px-8"
          >
            {isSubmitting ? "Saving..." : "Continue"}
          </Button>
        </div>
      </div>
    </div>
  )
}
